import { CategoriasService } from './../../categorias/categoria.service';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Location } from '@angular/common';

import { AbstractBaseComponent } from '../../../../config/core/abstract-base.component';
import { CategoriaDTO } from 'models/categoria.dto';
import { ProdutosService } from '../produtos.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';


@Component({
  templateUrl: './edit-produtos.component.html',
  styleUrls: ['./edit-produtos.component.scss']
})
export class EditProdutosComponent extends AbstractBaseComponent implements OnInit {


  public categoria: CategoriaDTO;


  constructor(
    public fb: FormBuilder,
    public route: ActivatedRoute,
    public location: Location,
    public CategoriasService: CategoriasService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.criarFormulario();
    this.route.params.forEach((params: Params) => {
      let id = params['id'];
      this.CategoriasService.findById(id)
      .subscribe(resp => {
        let cat = resp as CategoriaDTO;
        this.editFormulario(cat);
        },
          errors => console.log(errors.error)
        )
    });
  }

  confirmar() {
    this.formulario.patchValue({
      urlImagem: this.urlFoto
    });
    console.log(this.formulario.value)
    
    this.CategoriasService.atualizar(this.formulario.value)
      .subscribe(response => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria alterada com sucesso!!!');
        this.router.navigate(['/categorias/listar']);
      },
      error => {
        console.log(error);
      });
  }

  public voltar(){
    this.location.back();
  }
  
  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }
  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }


  private criarFormulario() {
    this.formulario = this.fb.group({
      id: [],
      nome: ['', Validators.required],
      status: ['A'],
      comentario: [''],
      foto: ['']
    })
  }
  private editFormulario(categoria: CategoriaDTO) {
    this.formulario = this.fb.group({
      id: [categoria.id],
      nome: [categoria.nome, [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
      status: [categoria.status],
      comentario: [categoria.comentario],
      urlImagem: [categoria.urlImagem]
    });
    console.log('alterado',categoria)
    this.urlFoto = categoria.urlImagem;
  }
}



