import { Injectable } from  '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';

import { API_CONFIG } from './../../../config/core/api.config';
import { CategoriaDTO } from 'models/categoria.dto';
import { AbstractService } from 'config/core/abstract-service';

import { Observable } from 'rxjs/Rx';
//import { catchError, retry } from 'rxjs/operators';


@Injectable()
export class CategoriasService extends AbstractService {

  constructor(public http: HttpClient){
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${API_CONFIG.baseUrl}/categorias/${id}`,this.getHearderToken() );
  }

  findAll(): Observable<CategoriaDTO[]> {
    return this.http.get<CategoriaDTO[]>(`${API_CONFIG.baseUrl}/categorias/all`);
  }

  savar(categoria: CategoriaDTO) {
    return this.http.post(`${API_CONFIG.baseUrl}/categorias`, JSON.stringify(categoria), this.getHearderTokenNoResponse());
  }

  changeStatus(categoria: CategoriaDTO) {
    return this.http.put(`${API_CONFIG.baseUrl}/categorias/${categoria.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  atualizar(categoria: CategoriaDTO) {
    return this.http.put(`${API_CONFIG.baseUrl}/categorias/${categoria.id}`, JSON.stringify(categoria), this.getHearderTokenNoResponse());
  }

  excluir(categoria: CategoriaDTO): Observable<{}> {
    let url = `${API_CONFIG.baseUrl}/categorias/${categoria.id}`;
    return this.http.delete(url, this.getHearderToken());
  }

  insert(obj: any) {
    return this.http.post(`${API_CONFIG.baseUrl}/pedidos`, obj, { observe: 'response', responseType: 'text' });
  }
}



