import { NgModule, LOCALE_ID } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';


import { PopupModalModule } from 'app/shared/modal/popup-modal.module';
import { ProdutosService } from './produtos.service';
import { CategoriasService } from './../categorias/categoria.service';
import { SharedComponentModule } from './../../shared/shared-component.module';
import { ProdutosRoutingModule } from './produtos-routing.module';

import { ListarProdutosComponent } from './listar/listar-produtos.component';
import { CreateProdutosComponent } from './create/create-produtos.component';
import { EditProdutosComponent } from './edit/edit-produtos.component';
import { StorageService } from './../../../services/storage.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    ProdutosRoutingModule

  ],
  declarations: [
    ListarProdutosComponent,
    CreateProdutosComponent,
    EditProdutosComponent
  ],
  providers:[
    ProdutosService,
    CategoriasService,
    StorageService,
    {provide: LOCALE_ID, useValue: 'pt-BR'}
  ]
})
export class ProdutosModule { }



