import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { create } from 'domain';


import { CreateProdutosComponent } from './create/create-produtos.component';
import { ListarProdutosComponent } from './listar/listar-produtos.component';
import { EditProdutosComponent }   from './edit/edit-produtos.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'listar',
    pathMatch: 'full',
  },
  {
    path: '',
    data: {
      title: 'Produtos'
    },
    children: [
      {
        path: 'listar',
        component: ListarProdutosComponent,
        data: {
          title: 'Listar'
        }
      },
      {
        path: 'novo',
        component: CreateProdutosComponent,
        data: {
          title: 'Novo produto'
        }
      },
      {
        path: 'editar/:id',
        component: EditProdutosComponent,
        data: {
          title: 'Alterar produto'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProdutosRoutingModule { }



