import { Injectable } from  '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { API_CONFIG } from './../../../config/core/api.config';
import { AbstractService } from 'config/core/abstract-service';

import { ProdutoDTO } from 'models/produto.dto';
//import { catchError, retry } from 'rxjs/operators';


@Injectable()
export class ProdutosService extends AbstractService {

  constructor(public http: HttpClient){
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${API_CONFIG.baseUrl}/produtos/${id}`,this.getHearderToken() );
  }

  findAll(): Observable<ProdutoDTO[]> {
    return this.http.get<ProdutoDTO[]>(`${API_CONFIG.baseUrl}/produtos/`);
  }

  savar(produto: ProdutoDTO) {
    return this.http.post(`${API_CONFIG.baseUrl}/produtos`, JSON.stringify(produto), this.getHearderTokenNoResponse());
  }

  changeStatus(produto: ProdutoDTO) {
    return this.http.put(`${API_CONFIG.baseUrl}/produtos/${produto.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  atualizar(produto: ProdutoDTO) {
    return this.http.put(`${API_CONFIG.baseUrl}/produtos/${produto.id}`, JSON.stringify(produto), this.getHearderTokenNoResponse());
  }

  excluir(produto: ProdutoDTO): Observable<{}> {
    let url = `${API_CONFIG.baseUrl}/produtos/${produto.id}`;
    return this.http.delete(url, this.getHearderToken());
  }

}



