import { EventEmitter }     from '@angular/core';
import { ToasterMensagem } from 'models/toater-mensagem.model';
import { TipoMensagemEnum } from '../../../models/enums/tipo-mensagem.enum';

export class EventEmitterService {

    private static emitters: {[nomeEvento: string]: EventEmitter<any>} = {};

    static get (nomeEvento:string): EventEmitter<any> {
        if (!this.emitters[nomeEvento]){
            this.emitters[nomeEvento] = new EventEmitter<any>();
        }
        return this.emitters[nomeEvento];
    }

    static unsubscribeEvent(){
        this.emitters = {};
        this.getToater();
    }

    static getToater(){
        return this.get('showToasterEvent');
    }

    static emitirMensagemToaster(tm: ToasterMensagem){
        return this.get('showToasterEvent').emit(tm);
    }

    static emitirMensagemToasterSucesso(titulo: string, mensagem:string){
        let tm = new ToasterMensagem(TipoMensagemEnum.success, titulo,  mensagem);
        return this.get('showToasterEvent').emit(tm);
    }

    static emitirMensagemToasterAlerta(titulo: string, mensagem:string){
        let tm = new ToasterMensagem(TipoMensagemEnum.warning, titulo, mensagem);
        return this.get('showToasterEvent').emit(tm);
    }

    static emitirMensagemToasterInfo(titulo:string, mensagem:string){
        let tm = new ToasterMensagem(TipoMensagemEnum.warning, titulo, mensagem);
        return this.get('showToasterEvent').emit(tm);
    }
}



