import { CategoriasService } from './../../categorias/categoria.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { CategoriaDTO } from 'models/categoria.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { AbstractBaseComponent } from '../../../../config/core/abstract-base.component';
import { ProdutosService } from '../produtos.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';


@Component({
  selector: 'app-create-catetorias',
  templateUrl: './create-produtos.component.html',
  styleUrls: ['./create-produtos.component.scss']
})
export class CreateProdutosComponent extends AbstractBaseComponent implements OnInit {


  public categoria: CategoriaDTO;


  constructor(
      public fb: FormBuilder, 
      public location: Location,
      public CategoriasService: CategoriasService, 
      public router: Router) {
    super();
  }

  ngOnInit() {
    this.criarFormulario();
  }

  confirmar() {
    this.formulario.patchValue({
      urlImagem: this.urlFoto
    });
    this.CategoriasService.savar(this.formulario.value)
      .subscribe(response => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria incluída com sucesso!!!');
        this.router.navigate(['/categorias/listar']);
      },
      error => {
        console.log('error de validação:', error.error);
      });
  }

  public voltar(){
    this.location.back();
  }
  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }


  private criarFormulario() {
    this.formulario = this.fb.group({
      id: [],
      nome: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
      status: ['A'],
      comentario: [''],
      urlImagem: ['']
    })
  }
}



